import Vue from "vue";
import Router from "vue-router";
import Vuetable from "vuetable-2";

import Join from "./components/Join";
import Create from "./components/Create";

Vue.use(Router);
Vue.use(Vuetable);

export default new Router({
  routes: [
    {
      path: "/",
      name: "join",
      component: Join
    },
    {
      path: "/create",
      name: "create",
      component: Create
    }
  ]
});
