<template>
    <div id="select_party" v-if="!found">
      <label>Enter party ID : </label>
      <input type="text" v-model="party_id" />
    </div>
    <div id="play" v-else>
      <Play :party_id="party_id"></Play>
    </div>
</template>

<script>
import Play from "./Play";

import {db} from '../firebaseConfig.js';

export default {
  name: "join",
  components: {
    Play
  },
  data() {
    return {
      party_id: null,
      found: false,
    }
  },
  watch: {
    party_id() {
      db.ref('games/'+this.party_id).once('value').then((snap) => {
        if(snap.exists()) {
          this.found = true;
        }
      })
    }
  }
};
</script>
