<template>
    <div id="card">
        <table>
            <tr v-for="(row, i) in matrixSongs" :key="i">
                <td v-for="(col, j) in row" :key="j">
                    <button :disabled="alreadyDone[i][j]" @click="clickSong(i,j,matrixSongs[i][j])">{{matrixSongs[i][j].name}}</button>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>

export default {
  name: "card",
  props: ["songs", "type", "currentSong"],
  computed: {
    matrixSongs() {
      let matrix = new Array(5);
      const size_songs = this.songs.length;
      for (let i = 0; i < 5; i++) {
        matrix[i] = new Array(5);
        for (let j = 0; j < 5; j++) {
          const index = Math.floor(Math.random() * Math.floor(size_songs));
          matrix[i][j] = this.songs[index];
        }
      }

      return matrix;
    },
    alreadyDone() {
      let matrix = new Array(5);
      for (let i = 0; i < 5;i++) {
        matrix[i] = new Array(5);
        for (let j = 0;j<5;j++) {
          matrix[i][j] = false;
        }
      }

      return matrix;
    }
  },
  methods: {
    clickSong(i, j, song) {
      if(this.currentSong.url == song.url) {
        this.alreadyDone[i][j] = true;
        this.$forceUpdate();
        if(this.checkWon() == true) {
          console.log("Checking ...")
          this.partyWon();
        }
      }
    },
    checkWon() {
        //TODO : we can modify way of winning here
        console.log("Checking won !");
        switch(this.type) {
          case 'blackout':
            for (let i = 0;i<5;i++) {
              for (let j = 0; j<5;j++) {
                if(this.alreadyDone[i][j] == false)
                  return false;
              }
            }
            return true;
            break;
          default:
            return false;
        }
    },
    partyWon() {
      alert('Party won !!')
      this.$emit('partyWon');
    }
  }
};
</script>
