<template>
    <div id="play">
        <Card v-if="songs!=null" :songs="songs" :type="type" :currentSong="currentSong"></Card>
    </div>
</template>

<script>
import Card from "./Card";

import { db } from "../firebaseConfig.js";

export default {
  name: "play",
  components: {
    Card
  },
  data() {
    return {
      party_name: "",
      nb_player: 0,
      type: "",
      playlist: "",
      songs: null,
      currentSong: null,
    };
  },
  props: ["party_id"],
  methods: {
    fetchPartyAndPlaylist() {
      db.ref("games/" + this.party_id)
        .once("value")
        .then(snap => {
          this.party_name = snap.val().party_name;
          this.nb_player = snap.val().nb_player;
          this.type = snap.val().type;
          this.playlist = snap.val().playlist;
          db.ref("Playlists/" + this.playlist)
            .once("value")
            .then(snap => {
              this.songs = snap.val().songs;
            });
        });

      db.ref('games/' + this.party_id + '/currentSong').on('value',(snap) => {
        this.currentSong = snap.val();
      });
    }
  },
  created() {
    this.fetchPartyAndPlaylist();
  }
};
</script>
