<template>
    <div id="song">
        <iframe width="560" height="315" :src="url_encoded" frameborder="1" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"></iframe>
    </div>
</template>

<script>
export default {
  name: "song",
  computed: {
    url_encoded() {
      return 'https://www.youtube.com/embed/' + this.url.substring(32);
    }
  },
  props: ['url']
};
</script>
