<template>
    <div id="create" v-if="!party_id">
        <label>Number of players : </label>
        <input type="number" v-model="nb_player"/>
        <br/>

        <label>Party name : </label>
        <input type="text" v-model="party_name"/>
        <br/>

        <label>Playlist : </label>
        <select v-model="playlist">
            <option value="00">00's</option>
        </select>
        <br/>

        <label>Type : </label>
        <select v-model="type">
            <option value="T">T</option>
            <option value="blackout">Blackout</option>
        </select>
        <br/>

        <button @click="submitGame()">Create game</button>
    </div>
    <div id="manage_and_play" v-else>
      <p>Share this party id to your friends : {{party_id}}</p>
      <Play :party_id="party_id"></Play>
      <br/>
      <button id="nextSong" @click="nextSong()">Next song !</button>
      <Song v-if="currentSong" :url="currentSong.url"></Song>
    </div>
</template>

<script>
import Play from "./Play";
import Song from './Song';

import { db } from "../firebaseConfig.js";

export default {
  name: "create",
  data() {
    return {
      nb_player: 0,
      party_name: "",
      playlist: "",
      type: "",
      party_id: null,
      songs: null,
      currentSong : null
    };
  },
  components: {
    Play,
    Song
  },
  methods: {
    submitGame() {
      //TODO: check if all forms are ok

      const data = {
        nb_player: this.nb_player,
        party_name: this.party_name,
        playlist: this.playlist,
        type: this.type,
        currentSong: 0,
      };
      this.party_id = db.ref("games").push(data, err => {
        if (err) alert(err);
        db.ref('Playlists/' + this.playlist).once('value').then((snap) => {
          this.songs = snap.val().songs;
        });
      }).key;
    },
    nextSong() {
      this.currentSong = this.songs[Math.floor(Math.random() * Math.floor(this.songs.length))]
      db.ref('games/'+this.party_id).update({currentSong: this.currentSong});
    }
  }
};
</script>
